@import url(https://fonts.googleapis.com/css2?family=Lobster&family=Josefin+Sans&display=swap);
body{margin:0;font-family:-apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}*{margin:0;outline:none}

:root{--primaryFont: "Lobster", cursive;--secondaryFont: "Josefin Sans", sans-serif;--primary: #2a2b2a;--primaryLight: #313231;--primaryLightDisabled: #313231f8;--accent: #ff8966;--accentRipple: #ff8a667a;--accentHighRipple: #ff8a66af;--accentMidRipple: #ff8a6691;--accentHighlight: rgba(255, 138, 102, 0.2);--accentLowHighlight: rgba(255, 138, 102, 0.15);--accentLowHighlightDisabled: rgba(255, 138, 102, 0.05);--accentSecondary: #e5446d;--text: #918d7b;--textDisabled: #918d7b56;--error: #a60000;--success: #00c400be}#App{background-color:#313231;background-color:var(--primaryLight);min-height:100vh;font-family:"Josefin Sans", sans-serif;font-family:var(--secondaryFont);-webkit-user-select:none;-ms-user-select:none;user-select:none;display:grid;grid-template-rows:70px auto 60px}#App #switchContainer{display:flex;align-items:flex-start;justify-content:center}#App #switchContainer>*{margin-top:10vh}

#navBar{background-color:var(--primary);display:flex;align-items:center;justify-content:space-between;padding:0 10px;border-bottom:1px solid var(--accent)}#navBar a{color:var(--accent);font-family:var(--primaryFont);font-size:2.5rem;font-weight:500;cursor:pointer;text-decoration:none}#navBar div{height:100%;display:flex;align-items:center;justify-content:center}#navBar div a{display:flex;align-items:center;position:relative;height:100%;padding:0 10px;color:var(--accent);font-family:var(--secondaryFont);font-size:1rem;text-decoration:none}#navBar div a::before{content:"";display:block;position:absolute;width:60%;height:3px;bottom:10%;right:50%;transform:translateX(50%);background-color:var(--accentRipple);opacity:0;transition:opacity 500ms}#navBar div a:hover::before{opacity:1}@media only screen and (max-width: 250px){#navBar a{font-size:1.5rem}#navBar div a{font-size:.8rem;padding:0 5px}}@media only screen and (max-width: 180px){#navBar{flex-direction:column}#navBar div a{font-size:.8rem;padding:0 15px}}

#field{position:relative;display:flex;align-items:center;justify-content:space-between;width:85%;height:35px;margin:5px 0;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;box-shadow:none;transition:box-shadow 400ms}#field:hover,#field:focus-within{box-shadow:0 0 1px var(--accentHighRipple),inset 0 0 1px var(--accentHighRipple)}#field.invalid{box-shadow:0 0 1px var(--error),inset 0 0 1px var(--error)}#field.valid{box-shadow:0 0 1px var(--success),inset 0 0 1px var(--success)}#field input{height:100%;width:calc(100% - 15px);margin-left:10px;background-color:transparent;color:var(--text);border:0;font-family:var(--secondaryFont)}#field input::selection{background-color:var(--accentHighlight)}#field input.pass{width:calc(100% - 45px)}#field button{z-index:1;width:25px;height:25px;margin:0 5px 0 0;border:0;border-radius:50%;background-color:var(--primary);color:var(--text);padding:0;cursor:pointer}#field button:focus{box-shadow:none}#field button:hover{box-shadow:0 0 1px var(--accentHighlight),inset 0 0 1px var(--accentHighlight)}

#loginContainer{width:90%;max-width:300px;height:80%;max-height:300px;display:flex;flex-direction:column;align-items:center;justify-content:flex-end;background-color:var(--primary);border-radius:10px;border:1px solid var(--accent)}#loginContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#loginContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:80%}#loginContainer form a{color:var(--accentHighRipple);text-decoration:none;padding:4px 8px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#loginContainer form a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#loginContainer form #forgotPass{font-size:.8rem;margin-top:5px;margin-right:7%;align-self:flex-end}#loginContainer form #error{font-size:.7rem;color:var(--error);margin-top:5px;text-align:center}#loginContainer form>button{margin-top:20px;width:40%;height:35px;margin-bottom:15px;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;padding:0;transition:box-shadow 300ms}#loginContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#loginContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#loginContainer form #join{font-size:.8rem;color:var(--text);text-align:center}

#joinContainer{width:90%;max-width:300px;height:80%;max-height:400px;min-height:200px;display:flex;flex-direction:column;align-items:center;justify-content:flex-end;background-color:var(--primary);border-radius:10px;border:1px solid var(--accent)}#joinContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#joinContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:80%}#joinContainer form #error{font-size:.7rem;color:var(--error);margin-top:5px}#joinContainer form>button{margin-top:20px;width:40%;height:35px;margin-bottom:15px;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;padding:0;transition:box-shadow 300ms}#joinContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#joinContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#joinContainer form #login{font-size:.8rem;color:var(--text);text-align:center;margin-top:10px}#joinContainer form #login a{color:var(--accentHighRipple);text-decoration:none;padding:4px 8px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#joinContainer form #login a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}@media only screen and (max-width: 250px){#joinContainer{width:95%;height:90%}}

#forgotPassContainer{width:90%;max-width:320px;height:80%;max-height:300px;display:flex;flex-direction:column;align-items:center;justify-content:center;background-color:var(--primary);border-radius:10px;border:1px solid var(--accent)}#forgotPassContainer form{display:flex;flex-direction:column;align-items:center;justify-content:space-evenly;width:100%;height:80%}#forgotPassContainer form h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#forgotPassContainer form #desc{font-size:.9rem;color:var(--text);margin-bottom:5px;text-align:center}#forgotPassContainer form #error{font-size:.7rem;color:var(--error);margin-top:5px;text-align:center}#forgotPassContainer form>button{margin-top:20px;width:40%;height:35px;margin-bottom:15px;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;padding:0;transition:box-shadow 300ms}#forgotPassContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#forgotPassContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}#forgotPassContainer #login{font-size:.8rem;color:var(--text);text-align:center;margin-top:10px}#forgotPassContainer #login a{color:var(--accentHighRipple);text-decoration:none;padding:4px 8px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#forgotPassContainer #login a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}

#resetPassContainer{width:90%;max-width:300px;height:80%;max-height:250px;min-height:200px;display:flex;flex-direction:column;align-items:center;justify-content:flex-end;background-color:var(--primary);border-radius:10px;border:1px solid var(--accent)}#resetPassContainer h3{font-family:var(--primaryFont);font-size:2rem;color:var(--accent)}#resetPassContainer form{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;height:80%}#resetPassContainer form p{font-size:.7rem;color:var(--error);margin-top:5px}#resetPassContainer form>button{margin-top:20px;width:40%;height:35px;margin-bottom:15px;background-color:var(--primaryLight);border:1px solid var(--accentLowHighlight);border-radius:10px;color:var(--accent);font-family:var(--secondaryFont);cursor:pointer;padding:0;transition:box-shadow 300ms}#resetPassContainer form>button:hover{box-shadow:0 0 1px var(--accentRipple),0 0 1px var(--accentRipple)}#resetPassContainer form>button:disabled{background-color:var(--primaryDisabled);color:var(--textDisabled);box-shadow:0 0 1px var(--accentLowHighlightDisabled),0 0 1px var(--accentLowHighlightDisabled);cursor:default}

#policy{margin:0 5% 40px 5%;color:var(--text)}#policy a{color:var(--accentHighRipple);font-size:.9rem;text-decoration:none;padding:2px 6px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#policy a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#policy h1{margin-top:30px}#policy h2{margin-top:20px}#policy h3{margin-top:10px}#policy h1,#policy h2,#policy h3{margin-bottom:5px}

.icon *{fill:var(--accentRipple);transition:fill 200ms linear}.icon:hover *{fill:var(--accent)}

#footer{display:flex;align-items:center;justify-content:center;background-color:var(--primary);border-top:1px solid var(--accent)}#footer a{color:var(--accentHighRipple);font-size:.8rem;text-decoration:none;padding:5px 5px;border-radius:5px;transition:background-color 200ms linear, color 200ms linear}#footer a:hover{color:var(--accent);background-color:var(--accentLowHighlight)}#footer #contacts{display:flex;align-items:center;justify-content:center}#footer #separator{width:2px;height:50%;background-color:var(--accentHighlight);border-radius:20px;margin:0 15px}

